import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { BsTitle } from '../../atoms/title/title';
import { BsImage } from '../../atoms/image/image';

/**
 * Blog header component.
 */
@Component({
  tag: 'bs-blog-header',
  styleUrl: 'blog-header.scss',
  shadow: true,
})
export class BsBlogHeader {
  /**
   * A (json)object string containing the bs-image elemement properties.
   */
  @Prop() image: BsImage | string;

  @Memoize('image')
  get parsedImage(): BsImage {
    return parseIfString(this.image);
  }

  /**
   * A (json)object string containing the bs-image that is used for the logo elemement properties.
   */
  @Prop() logo: BsImage | string;

  @Memoize('logo')
  get parsedLogo(): BsImage {
    return parseIfString(this.logo);
  }

  /**
   * A (json)object string containing the bs-title elemement properties.
   */
  @Prop() head: BsTitle | string;

  @Memoize('head')
  get parsedTitle(): BsTitle {
    return parseIfString(this.head);
  }

  render() {
    return (
      <Host>
        <header class="blog-header">
          <bs-title
            variant={this.parsedTitle.variant}
            color={this.parsedTitle.color}
            tag={this.parsedTitle.tag}
            class="blog-header-title"
            text={this.parsedTitle.text}
          />
          <bs-image
            src={this.parsedImage.src}
            srcset={this.parsedImage.srcset}
            width={this.parsedImage.width}
            height={this.parsedImage.height}
            image-sizes={this.parsedImage.imageSizes}
            alt-text={this.parsedImage.altText}
            itemprop={this.parsedImage.itemprop}
            cover={this.parsedImage.cover}
          />
          <bs-logo-bar logo={this.parsedLogo} />
        </header>
      </Host>
    );
  }
}
